.box {
  background-color: rgba(255, 255, 255, 0.75);
  margin: 16px;
  padding: 16px;
  border-radius: 16px;
  text-align: center;
}
#root {
  text-shadow: -1px -1px 0 #000000, 1px -1px 0 #000, -1px 1px 0 #000,
    1px 1px 0 #000;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
}
.background {
  background-image: url("./assets/background1.jpg");
}
